/* Empty front matter tags above are required to signal to Jekyll that this file should be interpolated for Liquid Syntax */

@charset "utf-8";

// Define defaults for each variable.

$base-font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI",
  "Segoe UI Symbol", "Segoe UI Emoji", "Apple Color Emoji", Roboto, Helvetica,
  Arial, sans-serif !default;
$code-font-family: "Menlo", "Inconsolata", "Consolas", "Roboto Mono",
  "Ubuntu Mono", "Liberation Mono", "Courier New", monospace;
$base-font-size: 14px !default;
$base-font-weight: 400 !default;
$small-font-size: $base-font-size * 0.875 !default;
$base-line-height: 1.5 !default;

// $spacing-unit:     30px !default;
$spacing-unit: 5px !default;

$brand-color: #999999 !default;
$brand-color-light: lighten($brand-color, 5%) !default;
$brand-color-dark: darken($brand-color, 35%) !default;

$site-title-color: $brand-color-light !default;

$text-color: #bbbbbb !default;
$background-color: #181818 !default;
$code-background-color: #212121 !default;

$link-base-color: #79b8ff !default;
$link-visited-color: $link-base-color !default;
$link-hover-color: $text-color !default;

$border-color-01: $brand-color-dark !default;
$border-color-02: $brand-color-light !default;
$border-color-03: $brand-color !default;

$table-text-color: $text-color !default;
$table-zebra-color: lighten($background-color, 4%) !default;
$table-header-bg-color: lighten($background-color, 10%) !default;
$table-header-border: lighten($background-color, 21%) !default;
$table-border-color: $border-color-01 !default;

$table-text-align: left !default;

// Width of the content area
$content-width: 800px !default;

// Use media queries like this:
// @include media-query($on-palm) {
//   .wrapper {
//     padding-right: $spacing-unit / 2;
//     padding-left: $spacing-unit / 2;
//   }
// }
// Notice the following mixin uses max-width, in a deprecated, desktop-first
// approach, whereas media queries used elsewhere now use min-width.
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

$on-palm: 600px !default;
$on-laptop: 800px !default;

$on-medium: $on-palm !default;
$on-large: $on-laptop !default;

@import url("https://fonts.googleapis.com/css2?family=DM+Mono:wght@300;400;500&display=swap");

// card-list-width
$card-list-width: 414 * 450px;

body {
  padding: 0;
  margin: 0;
  background-color: #17141d;
  color: white;
  font-family: "DM Mono", monospace;
}

a {
  text-decoration: none;
}

.card-list {
  position: relative;
  width: 100%;
  margin-top: 145px;
  display: flex;
  padding: 3rem;
  overflow-x: hidden;
      /* for our fade effect */
  transition: all 0.5s ease-out;
  /* for paginate */
  list-style: none;
}

.fade-card-list {
    /* to fade out */
  opacity: 0;
}

.card-list::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
.card-list::-webkit-scrollbar-thumb {
  background: #201c29;
  border-radius: 10px;
  box-shadow: inset 2px 2px 2px hsla(0, 0%, 100%, 0.25),
    inset -2px -2px 2px rgba(0, 0, 0, 0.25);
}

.card-list::-webkit-scrollbar-track {
  background: linear-gradient(90deg, #201c29, #201c29 1px, #17141d 0, #17141d);
}

.card {
  height: 675px;
  width: 450px;
  min-width: 400px;
  padding: 1rem;
  border-radius: 16px;
  background: #49268f;
  box-shadow: -1rem 0 3rem #000;
  display: inline-block;
  flex-direction: row;
  transition: 0.2s;
  margin: 0;
  scroll-snap-align: start;
  clear: both;
  position: relative;
  opacity: 1;
}

